import React, { Suspense } from "react";
import Loader from '../components/Loader'
import { useSnapshot } from "valtio";
import state from "../store";
import TransitionLink from "../hooks/TransitionLink";
import jsonData from '../json/data.json'
export default function Intro() {
  const {menu} = jsonData
  const snap = useSnapshot(state);
  return (
    <div className="home flex h-screen w-screen flex-col items-center justify-center relative z-10">
      <Suspense fallback={<Loader />}>
        <div className="">
          <ul className="w-screen h-screen flex items-center justify-center">
            <li className="relative z-10 p-4 text-white backdrop-blur-lg text-center text-2xl Anuphan">
              <p>Select a <span className="Sedgwick design text-red-500 text-3xl">design</span></p>
              {/* <p className="text-sm">
                I could not decide on one so I made two
              </p> */}
            </li>
            <li className="flex-1 flex w-full h-full items-start justify-center clip-left absolute z-0 overflow-hidden">
              <p className="flex w-[50%] absolute left-0 top-0 z-10 text-7xl h-full justify-start items-start lg:items-center lg:justify-center pointer-events-none design text-gradB"> Figurative </p>
              <TransitionLink to={menu[2].route} className="w-full h-full absolute z-0 cursor-pointer">
                <img
                  className="home object-cover w-full h-full transition-all duration-1000 hover:scale-[1.05] absolute z-0"
                  src={snap.abstractBackground}
                  alt="creative"
                />
              </TransitionLink>
            </li>
            <li className=" flex-1 flex w-full h-full items-end justify-center clip-right absolute z-0 overflow-hidden invert">
              <p className="Sedgwick flex w-[50%] absolute right-0 top-0 z-10 text-7xl h-full justify-end items-end lg:items-center lg:justify-center pointer-events-none design text-gradB">Abstract</p>
              <TransitionLink to={menu[1].route} className="w-full h-full absolute z-0 cursor-pointer">
                <img
                  className="home object-cover w-full h-full transition-all duration-1000 hover:scale-[1.05] absolute z-0"
                  src={snap.abstractBackground}
                  alt="creative"
                />
              </TransitionLink>
            </li>
          </ul>
        </div>
      </Suspense>
    </div>
  );
}
